  _populateTablePredio(bodyTable, drawFunction) {
    // obtener del servicio los predios resultantes de la solicitud
    let urlPredioResults = `${selfCm.config.resultsByApplication}/${selfCm.codRequestsCm}`
    selfCm._callApiRestServices(urlPredioResults, {}).then(response => {
      try {
        // --------------------------------------------------------------
        // Comentar lineas cuando se tenga el servicio
        // response = responseDivision[selfCm.codRequestsCm]
        // --------------------------------------------------------------
        let idx = 1
        bodyTable.innerHTML = ''
        for (let predio of response.results) {
          let tr = dojo.create('tr');
          tr.id = `predio_${idx}_${predio['cpm']}`;
          let row = `<td class="center-aligned">${idx}</td>
                     <td>${predio['cpm']}</td>
                     <td>${predio['address']}</td>
                     <td class="center-aligned">
                      <span id="${tr.id}_draw"><i class="fas fa-map-marker-alt"></i></span>
                     </td>`
          tr.innerHTML = row;
          tr.style.cursor = "pointer";
          bodyTable.appendChild(tr)
          dojo.query(`#${tr.id}_draw`).on('click', drawFunction)
          idx = idx + 1
        }
      } catch (error) {
        selfCm.busyIndicator.hide();
        selfCm._showMessage(error.message, type = "error")
      }
    })
  },

