  _ordenarPoligonosNorteSur(poligonos, idx, bodyTable) {
    // Obtener la coordenada ms al norte de cada polgono
    let coordenadasNorte = poligonos.map(poligono => {
      let extent = poligono.getExtent();
      return extent.ymax;
    });

    // Ordenar los polgonos en base a la coordenada ms al norte
    let poligonosOrdenados = poligonos.slice().sort((a, b) => {
      let coordenadaNorteA = coordenadasNorte[poligonos.indexOf(a)];
      let coordenadaNorteB = coordenadasNorte[poligonos.indexOf(b)];
      return coordenadaNorteB - coordenadaNorteA; // Ordenar de norte a sur
    });

    let graphicLayerLabelCodLoteDivision = new GraphicsLayer({
      id: idGraphicLabelCodLote,
    });

    let font = new Font("20px", Font.STYLE_NORMAL, Font.VARIANT_NORMAL, Font.WEIGHT_BOLD, "Arial");

    let dataLoteTable = []

    // Paso 5: Agregar nmero de polgono a propiedad "order"
    poligonosOrdenados.forEach((poligono, index) => {

      const polygon = turf.polygon(poligono.rings);
      let center = turf.pointOnFeature(polygon);

      let pointLabel = new Point({
        x: center.geometry.coordinates[0],
        y: center.geometry.coordinates[1],
        spatialReference: { wkid: 4326 }
      })
      let cod_lote = selfCm._zfill(idx + 1, 3)
      let txtSym = new TextSymbol(cod_lote, font, new Color([250, 0, 0, 1]));
      txtSym.setColor(new esri.Color([0, 0, 0, 1])); // color blanco
      txtSym.setSize("12pt");
      txtSym.setHaloColor(new esri.Color([255, 255, 255, 1]));
      txtSym.setHaloSize(2);
      let idGraphic = `label_${index + 1}`
      let graphicLabel = new Graphic(
        pointLabel,
        txtSym,
        {
          id: idGraphic,
          lot_urb: selfCm.responseRequests[index].urbanLotNumber,
          clase: 'labelCodLoteDivision'
        });
      graphicLayerLabelCodLoteDivision.add(graphicLabel);
      dataLoteTable.push({ num: index + 1, id: idGraphic, cod_lote: cod_lote })
      idx = idx + 1
      // editToolbar.activate(Edit.MOVE, graphicLabel, {allowAddVertices: false, allowDeleteVertices: false});
    });


    // Completar table
    selfCm._buildDataLoteTable(bodyTable, dataLoteTable)
    selfCm.map.addLayer(graphicLayerLabelCodLoteDivision)
  },

